.benefits-wrapper {
	padding: 60px 0;
}

.benefits-row {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;

	column-gap: 20px;
	row-gap: 20px;

	padding: 20px 0;
}

.benefits-title {
	text-align: center;
}

.benefits-card {
	display: flex;
	width: 380px;
	height: 165px;
	flex-direction: row;
	column-gap: 10px;
	padding: 20px 20px;
	border-radius: 24px;
	box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
	/* box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px; */
}

.card-img {
	height: 50px;
	width: 50px;
}

.card-text {
	opacity: 0.6;
	max-width: 300px;
	font-size: 18px;
}

@media (max-width: 430px) {
	.card-text {
		font-size: 16px;
	}
}
